* {
  box-sizing: border-box;
  font-size: 16px;
  font-family: "Poppins", sans-serif;
  transition: all 0.6s;
}

::-webkit-scrollbar {
  width: 5px;
  background: $main-color;
}

::-webkit-scrollbar-thumb {
  background: $secondary-color;
}

.hidden {
  opacity: 0;

  &-left {
    transform: translateX(-100%);
  }
}

.show {
  opacity: 1;
  transform: translateX(0%);
  transition: all 1.5s;
}

